import React from 'react'
//  @ts-ignore
import theme from './theme.css';
import { Grid, Paper } from '@material-ui/core';
//@ts-ignore
import Slot from 'react-slot-machine'

interface prop {
  text: string; turning: boolean
}

const SlotBox = (target: {
  target: string
}) => {
  return (
    <Slot /* props */ className='slot-box' target={() => parseInt(target)}>
      {
        list.map(value =>
          <div className='reel'>
            {value}
          </div>
        )
        // Children of `Slot` be sure to be `width` and `height` are 100%.
      }
    </Slot>
  )
}
const list = [1, 2, 3, 4, 5, 6, 7, 8, 9, 0]
const SlotMachine = ({ text, turning }: prop) => {
  console.log(text)
  return (<div>
    <Grid container spacing={3}>
      <Grid item xs>
        <Paper >
          <SlotBox target={text.substring(0, 1)} turning={turning}></SlotBox>
        </Paper>
      </Grid>
      <Grid item xs>
        <Paper >
          {/* <Reel text={text.substring(1, 2)} theme={theme} /> */}
        </Paper>
      </Grid>
      <Grid item xs>
        <Paper >
          {/* <Reel text={text.substring(2, 3)} theme={theme} /> */}
        </Paper>
      </Grid>
      <Grid item xs>
        <Paper >
          {/* <Reel text={text.substring(3, 4)} theme={theme} /> */}
        </Paper>
      </Grid>
    </Grid>
  </div >)
}
export default SlotMachine